// main page styles
.promo {
	overflow: hidden;
	&__list {
		opacity: 0;
		transition: opacity 200ms;
		visibility: hidden;
		&.loaded {
			opacity: 1;
			visibility: visible; } }
	&__item {
		display: block;
		font-size: 0;
		img {
			display: block;
			width: 100%;
			height: auto; } }
	&__wrap {
		padding: 130px 0 0 70px;
		@include r(768) {
			padding: 170px 10px 0; } }
	&__channel {
		margin-bottom: 20px;
		@include r(1024) {
			margin-bottom: 10px; }
		&-pic {
			max-height: 50px;
			vertical-align: top;
			@include r(1024) {
				max-height: 46px; } } }
	&__info {
		display: table;
		padding: 20px 25px 22px;
		background: #D62228;
		color: #fff;
		text-transform: uppercase;
		line-height: 1;
		@include r(1024) {
			padding: 20px 20px 22px; } }
	&__title {
		margin-bottom: 5px;
		font-size: 42px;
		font-weight: 700;
		@include r(1024) {
			font-size: 34px; }
		@include r(768) {
			font-size: 24px; } }
	&__text {
		font-size: 24px;
		@include r(1024) {
			font-size: 18px; }
		@include r(768) {
			font-size: 14px; } }
	&__when {
		display: table;
		padding: 12px 25px;
		background: #fff;
		font-size: 0;
		font-weight: 700;
		color: #D62228;
		text-transform: uppercase;
		line-height: 1;
		@include r(1024) {
			padding: 12px 20px; } }
	&__date,
	&__time {
		display: inline-block;
		font-size: 13px;
		@include r(1024) {
			font-size: 12px; } }
	&__date {
		margin-right: 10px;
		padding-right: 10px;
		border-right: 2px solid; }
	.slick-prev,
	.slick-next {
		width: 30px;
		height: 30px;
		border-radius: 50%;
		color: $cl; }
	.slick-prev {
		left: 40px;
		@include r(1160) {
			left: 30px; }
		@include r(768) {
			left: 0; }
		&:before {
			position: absolute;
			top: 0;
			left: 0;
			width: 30px;
			height: 30px;
			border-radius: 50%;
			border: 2px solid #fff;
			background: url("../img/arr-left-white.svg") center no-repeat; }
		&:after {
			content: "";
			opacity: 0;
			position: absolute;
			top: 0;
			left: 0;
			width: 30px;
			height: 30px;
			border-radius: 50%;
			border: 2px solid #fff;
			background: url("../img/arr-left.svg") center no-repeat; }
		&:hover {
			background: #fff;
			&:before {
				opacity: 0; }
			&:after {
				opacity: 1; } } }
	.slick-next {
		right: 40px;
		@include r(1160) {
			right: 30px; }
		@include r(768) {
			right: 0; }
		&:before {
			position: absolute;
			top: 0;
			left: 0;
			width: 30px;
			height: 30px;
			border-radius: 50%;
			border: 2px solid #fff;
			background: url("../img/arr-right-white.svg") center no-repeat; }
		&:after {
			opacity: 0;
			position: absolute;
			top: 0;
			left: 0;
			width: 30px;
			height: 30px;
			border-radius: 50%;
			border: 2px solid #fff;
			background: url("../img/arr-right.svg") center no-repeat;
			z-index: 3; }
		&:hover {
			background: #fff;
			&:before {
				opacity: 0; }
			&:after {
				opacity: 1; } } }
	.slick-dots {
		position: absolute;
		left: 50%;
		bottom: 35px;
		padding: 0;
		transform: translateX(-50%);
		@include r(1024) {
			bottom: 15px; } } }

.search {
	padding: 25px 0;
	background: url(../img/bg-search.jpg) no-repeat 50% 50%;
	background-size: cover;
	color: #FFF;
	@include r(1024) {
		padding: 30px 0; }
	&__wrap {
		max-width: 620px;
		margin: 0 auto;
		text-align: center;
		@include r(1024) {
			max-width: 520px; } }
	&__title {
		margin-bottom: 15px; }
	&__form {
		position: relative;
		margin-bottom: 20px;
		border-radius: 3px;
		background: #FFF; }
	&__input {
		position: relative;
		width: 100%;
		height: 50px;
		padding: 0 15px 0 50px;
		background: none;
		font-size: 20px;
		z-index: 2; }
	&__icon {
		position: absolute;
		top: 16px;
		left: 16px;
		font-size: 0;
		color: $cl;
		.icon-search {
			font-size: 18px; } }
	&__text {
		max-width: 440px;
		margin: 0 auto;
		font-size: 16px;
		font-weight: 300; } }

.section {
	padding: 40px 0 50px;
	&_blue {
		background: #1773c3;
		color: #FFF;
		@include r(768) {
			padding-bottom: 40px; } }
	&_banners {
		padding-bottom: 40px;
		background: #e5e5e6; }
	&_slider {
		padding-bottom: 15px;
		background: #f2f2f3; } }

