.more {
	text-align: center;
	&__btn {
		height: 42px;
		padding: 0 20px;
		border: 2px solid #adafb1;
		border-radius: 3px;
		font-size: 0; }
	&__icon,
	&__title {
		display: inline-block;
		vertical-align: middle; }
	&__icon {
		padding: 2px 15px 0 0;
		font-size: 12px; }
	&__title {
		font-size: 14px;
		text-transform: uppercase;
		font-weight: 700; } }

.label {
	position: absolute;
	bottom: 0;
	left: 0;
	right: 0;
	padding: 10px 20px 10px 10px;
	font-size: 12px;
	background: rgba(#000, 0.5);
	&__title {
		position: relative;
		z-index: 2; } }

.premieres {
	&__row {
		@include r(1024) {
			margin: 0 5px; }
		@include r(768) {
			margin: 0 -5px; } }
	&__group {
		margin-bottom: 30px; }
	&__head {
		margin-bottom: 25px;
		&-title {
			margin-bottom: 5px;
			font-size: 30px;
			font-weight: 700;
			color: #000;
			@include r(1024) {
				margin-bottom: 10px; }
			@include r(768) {
				font-size: 24px; } }
		&-text {
			font-size: 16px;
			font-weight: 300; }
		&-col {
			float: right;
			@include r(1024) {
				position: relative; }
			&:first-child {
				@include r(1024) {
					width: 50%;
					z-index: 2; }
				@include r(768) {
					display: none; } }
			&:last-child {
				@include r(1024) {
					width: 100%;
					margin-top: -35px; }
				@include r(768) {
					margin-top: 0; } } } }
	&__detail {
		display: table;
		float: right;
		margin-top: 44px;
		font-size: 0;
		@include r(1024) {
			margin-top: 0; }
		&-title,
		&-icon {
			display: table-cell;
			vertical-align: middle; }
		&-title {
			font-size: 16px;
			font-weight: 700; }
		&-icon {
			padding-left: 10px;
			.icon {
				font-size: 16px; } } }
	&__item {
		padding: 0 15px; }
	&__link {
		display: block;
		color: #fff; }
	&__preview {
		position: relative;
		margin-bottom: 10px; }
	&__pic {
		display: block;
		width: 100%; }
	&__when {
		position: absolute;
		right: 0;
		bottom: 0;
		padding: 15px;
		text-shadow: 0 0 1px rgba(#000,.7);
		text-align: center; }
	&__time {
		font-size: 24px;
		font-weight: 700;
		line-height: 1.1; }
	&__date {
		font-size: 12px; }
	&__title {
		font-size: 16px;
		font-weight: 700;
		@include r(768) {
			font-size: 18px; } }
	&__text {
		font-size: 13px;
		font-style: italic;
		@include r(768) {
			font-size: 14px; } }
	&__grid-col {
		&.col-50 {
			.movie__poster {
				height: 430px; } }
		&.col-25 {
			.movie {
				margin-bottom: 30px; } }
		@include r(1024) {
			width: 50%;
			.movie {
				margin-bottom: 30px;
				.movie__poster {
					height: auto;
					img {
						position: relative;
						top: auto;
						left: auto;
						transform: translate(0, 0);
						width: 100%;
						height: auto;
						min-height: 1px; } } } }
		@include r(480) {
			width: 100%; }
		&:first-child {
			@include r(1024) {
				display: none;
				width: 100%; }
			@include r(480) {
				display: block; } }
		&:nth-child(4) {
			@include r(1024) {
				clear: both; } } }
	&__grid-col:first-child &__link {
		@include r(480) {
			padding-bottom: 10px; } }
	&__grid-col:not(:first-child) &__link {
		@include r(480) {
			padding: 10px 0;
			border-top: 1px solid #E1E1E1; } }
	&__grid-col:not(:first-child) &__preview {
		@include r(480) {
			float: left;
			width: 60px;
			padding-right: 10px; } }
	&__grid-col:not(:first-child) &__label {
		@include r(480) {
			display: none; } }
	&__grid-col:not(:first-child) &__pic {
		@include r(480) {
			display: none; } }
	&__grid-col:not(:first-child) &__when {
		@include r(480) {
			position: relative;
			padding: 0;
			color: $cl;
			text-align: left;
			text-shadow: none; } }
	&__grid-col:not(:first-child) &__time {
		@include r(480) {
			font-size: 16px; } }
	&__grid-col:not(:first-child) &__date {
		@include r(480) {
			font-size: 12px; }
		@include text-overflow; }
	&__grid-col:not(:first-child) &__content {
		@include r(480) {
			display: table; } }
	&__grid-col:not(:first-child) &__title {
		@include r(480) {
			font-size: 14px; } }
	&__grid-col:not(:first-child) &__text {
		@include r(480) {
			font-size: 12px; } }
	&__grid-more {
		display: none;
		padding: 15px 20px;
		background: #F3F3F3;
		font-size: 16px;
		font-weight: 700;
		text-align: center;
		@include r(480) {
			display: block; } }
	&__grid &__link {
		@include r(1024) {
			margin-bottom: 35px; }
		@include r(480) {
			margin: 0; } }
	&__grid &__label {
		padding: 7px 15px 7px 10px; }
	&__grid &__content {
		min-height: 80px;
		padding-bottom: 10px;
		@include r(1024) {
			min-height: 0;
			padding-bottom: 0; } }
	&__grid &__title {
		color: $cl; }
	&__grid &__text {
		color: #7F7F7F; }
	.slick-prev,
	.slick-next {
		width: 30px;
		height: 44px;
		margin-top: -22px;
		transform: translateY(-130%);
		color: #FFF;
		&:before {
			font-size: 20px; }
		&:hover {
			background: #FFF;
			color: #1773c3; } } }

.selfpromo {
	display: block;
	background-size: cover;
	background-position: 50% 50%;
	&__center {
		max-width: 1366px;
		margin: 0 auto;
		height: 120px;
		@include vertical;
		padding: 0 15px;
		@include r(480) {
			height: auto;
			padding: 15px; } }
	&__content {
		display: inline-block;
		font-size: 36px;
		vertical-align: middle;
		color: #FFF;
		@include clr; }
	&__logo {
		position: relative;
		float: left;
		top: -10px;
		width: 230px;
		img {
			display: block;
			width: 100%; }
		p {
			position: absolute;
			bottom: -10px;
			right: 0;
			font-size: 16px; }
		@include r(1280) {
			width: 180px;
			p {
				font-size: 14px; } }
		@include r(480) {
			float: none;
			width: 100%;
			top: 0;
			margin-bottom: 20px;
			text-align: center;
			img {
				max-width: 180px; }
			p {
				right: 18px; } } }
	&__channels {
		overflow: hidden;
		padding: 11px 5% 0;
		font-size: 0;
		li {
			display: inline-block;
			vertical-align: middle;
			width: 20%;
			padding-left: 3%;
			&:first-child {
				padding-left: 0; }
			img {
				display: block;
				width: 100%; } }
		@include r(1023) {
			display: none; } }
	&__btn {
		float: right;
		width: 200px;
		.n-btn {
			max-width: 100%; }
		@include r(1023) {
			margin-left: 50px; }
		@include r(480) {
			float: none;
			margin: 0 auto; } } }

.banner {
	display: block;
	min-height: 210px;
	background-size: cover;
	background-position: 50% 50%;
	color: #FFF;
	&__wrap {
		padding: 30px; }
	&__title {
		margin-bottom: 8px;
		font-weight: 300;
		text-transform: uppercase; }
	&__text {
		max-width: 210px;
		margin-bottom: 20px; }
	&__btn {
		min-width: 205px;
		height: 55px;
		background: #FFF;
		border: 2px solid #fff;
		border-radius: 3px;
		font-size: 16px;
		text-transform: uppercase;
		font-weight: 700;
		color: #000; }
	&__logo {
		height: 30px;
		@include vertical;
		&-pic {
			vertical-align: middle; } }
	&_link {
		min-height: 90px;
		text-align: center; }
	&_link &__wrap {
		padding: 15px 30px; }
	&_catalog &__btn {
		background: transparent;
		color: #fff; } }

.banners {
	margin-bottom: 30px;
	&__list {
		&-col {
			&:nth-child(1) {
				margin-bottom: 30px;
				@include r(600) {
					margin-bottom: 20px; } } } }
	&__row {
		@include r(1024) {
			display: flex;
			flex-flow: wrap; } }
	&__col {
		&:first-child,
