			@include r(1024) {
				width: 50%;
				margin-bottom: 30px; }
			@include r(600) {
				width: 100%;
				margin-bottom: 20px; } }
		&:nth-child(2) {
			@include r(1024) {
				width: 100%;
				order: 3; } } } }


