		@include r(768) {
			right: -15px; }
		@include r(568) {
			right: -10px; }
		&:before {
			position: absolute;
			top: 0;
			left: 0;
			width: 30px;
			height: 30px;
			border-radius: 50%;
			border: 2px solid #dadada;
			background: url("../img/arr-right-gray.svg") center no-repeat; }
		&:after {
			opacity: 0;
			position: absolute;
			top: 0;
			left: 0;
			width: 30px;
			height: 30px;
			border-radius: 50%;
			border: 2px solid #dadada;
			background: url("../img/arr-right-white.svg") center no-repeat;
			z-index: 3; }
		&:hover {
			background: #dadada;
			&:before {
				opacity: 0; }
			&:after {
				opacity: 1; } } } }

// card
.section_premiere {
	padding: 40px 0 80px;
	@include g(#805eac, #55387a);
	color: #fff;
	.title {
		margin-bottom: 70px; } }
.slider {
