			height: 30px;
			border-radius: 50%;
			border: 2px solid #fff;
			background: url("../img/arr-left.svg") center no-repeat; }
		&:hover {
			background: #fff; } }
	.slick-next {
		right: -80px;
		@include r(1024) {
			right: -45px; }
		@include r(768) {
			right: -30px; }
		&:before {
			position: absolute;
			top: 0;
			left: 0;
			width: 30px;
			height: 30px;
			border-radius: 50%;
			border: 2px solid #fff;
			background: url("../img/arr-right-white.svg") center no-repeat; }
		&:after {
			position: absolute;
			top: 0;
			left: 0;
			width: 30px;
			height: 30px;
			border-radius: 50%;
			border: 2px solid #fff;
			background: url("../img/arr-right.svg") center no-repeat; }
		&:hover {
			background: #fff; } }
	.card:not(.slick-active) + .card.slick-active {
		.card__in {
			&:before {
				content: "";
				position: absolute;
				top: 0;
				left: 0;
				height: 100px;
				width: 1px;
				background: rgba(#e6e6e6, .5); } } } }


.section_channels {
	padding: 40px 0 20px;
	background: #f2f2f3;
	@include r(1280) {
		padding: 40px 30px 20px; }
	@include r(768) {
		padding: 40px 20px 20px; }
	@include r(567) {
		padding: 40px 15px 20px; } }

// content
.out {
	background: #e9e9e9;
	padding-bottom: 30px;
	&.out_film {
		padding-bottom: 0; } }
.blurred {
	position: absolute;
	top: -5px;
	bottom: -5px;
	left: -5px;
	right: -5px;
	@include cover;
	filter: blur(5px);
	z-index: 1; }
.topper {
	position: relative;
	padding: 50px 0 0;
	overflow: hidden;
	@include cover;
	&.topper_big {
		min-height: 300px; }
	.topper__in {
		position: relative;
		z-index: 2; }
	.topper__head {
		@include clr; }
	.topper__left {
		float: left;
		width: 50%;
		.h1 {
			text-shadow: 0 1px 1px rgba(72, 54, 110, 0.75); }
		// +r(1024)
		// 	min-height: 160px
		// +r(900)
		// 	min-height: auto
		@include r(600) {
			float: none;
			width: 100%; } }
	.topper__right {
		float: right;
		width: 50%;
		.n-socials {
			text-align: right;
			margin-top: 10px; }
		@include r(600) {
			float: none;
			width: 100%;
			.n-socials {
				text-align: left; } } }
	.topper__bottom {
		position: relative; } }
.n-socials {
	font-size: 0;
	li {
		display: inline-block;
		vertical-align: middle;
		margin-left: 45px;
		&:first-child {
			margin-left: 0; }
		a {
			font-size: 24px;
			color: #fff;
			transition: color 200ms;
			&:hover {
				color: $orange; }
			.icon-vk {
				font-size: 28px; } } }
	.icon {
		-webkit-filter: drop-shadow( 1px 1px 0px rgba(0,0,0,.5) );
		filter: drop-shadow( 1px 1px 0px rgba(0,0,0,.5) ); }
	@include r(768) {
		li {
			margin-left: 25px;
			a {
				font-size: 20px; } } } }
.s {
	position: relative;
	margin-bottom: 110px;
	margin-top: 20px;
	@include r(768) {
		margin-bottom: 44px; }
	form {
		position: relative;
		display: block;
		max-width: 450px;
		@include r(500) {
			max-width: 100%;
			width: 100%; } }
	.s__field {
		position: relative;
		input {
			width: 450px;
			height: 44px;
			border-radius: 22px;
			border: 2px solid #fff;
			padding: 0 40px 0 20px;
			background: transparent;
			font-size: 15px;
			font-weight: 500;
			color: #fff;
			@include placeholder {
				color: #fff; }
			&:focus {
				@include placeholder {
					font-size: 0; } }
			@include r(500) {
				width: 100%; } } }
	.s__submit {
		position: absolute;
		top: 10px;
		right: 15px;
		font-size: 22px;
		color: #fff; } }

// breadcrumbs
.breadcrumbs {
	margin-bottom: 15px;
	text-align: left;
	ul {
		font-size: 0;
		li {
			display: inline-block;
			vertical-align: middle;
			font-size: 12px;
			color: rgba(#fff, 0.5);
			a {
				color: rgba(#fff, 0.5);
				text-decoration: underline;
				&:hover {
					text-decoration: none; } }
			&:before {
				content: "/";
				display: inline-block;
				vertical-align: middle;
				margin: 0 3px; }
			&:first-child:before {
				display: none; } } } }
// main
.main {
	position: relative;
	z-index: 2; }
.nav {
	position: relative;
	padding: 0 60px;
	background: #f8f7f7;
	border-bottom: 1px solid #e7e5e5;
	@include r(1024) {
		padding: 5px 30px; }
	@include r(768) {
		padding: 10px 20px; }
	@include r(480) {
		padding: 0 10px 10px; }
	ul {
		margin: 0 -20px;
		font-size: 0;
		li {
			display: inline-block;
			margin: 0 20px;
			a {
				display: block;
				height: 86px;
				padding-top: 30px;
				font-weight: 500;
				font-size: 18px;
				line-height: 24px;
				color: #222121;
				transition: color 200ms, border-color 200ms;
				border-bottom: 2px solid #f8f7f7;
				&.active, &:hover {
					color: $orange;
					border-color: $orange; } } }
		@include r(1024) {
			text-align: left;
			li {
				a {
					height: 50px;
					padding-top: 15px;
					font-size: 16px; } } }
		@include r(768) {
			margin: 0;
			li {
				vertical-align: top;
				padding-right: 10px;
				width: 50%;
				margin: 0;
				a {
					display: inline-block;
					height: auto;
					font-size: 14px;
					line-height: 1.2; } } } } }
.catalog {
	padding: 40px 60px;
	background: #fff;
	&.catalog_top {
		padding: 40px;
		margin-top: -100px; }
	.catalog__in {
		font-size: 0;
		.card {
			display: inline-block;
			vertical-align: top;
			width: 25%;
			margin: 0 0 50px;
			// &_backface
			// 	margin-bottom: 80px
			@include r(768) {
				width: 50%; } } }
	@include r(1024) {
		padding: 40px 30px; }
	@include r(768) {
		padding: 30px 20px;
		.catalog__in {
			.card {
				width: 50%; } } }
	@include r(480) {
		padding: 50px 10px 20px;
		.catalog__in {
			.card {
				width: 50%; } } }
	@include r(414) {
		.catalog__in {
			.card {
				width: 100%;
				.card__poster {
					padding: 0; } } } } }

// filters
.filters {
	position: absolute;
	bottom: 0;
	right: 0;
	width: 360px;
	padding: 25px 0 25px 25px;
	background: #f8b636;
	.filters__title {
		margin-bottom: 20px; }
	.filters__row {
		margin-top: 15px;
		@include clr;
		&:first-child {
			margin-top: 0; } }
	.filters__left {
		float: left;
		width: 140px; }
	.filters__right {
		overflow: hidden;
		padding-right: 10px; }
	.filter__item {
		margin-bottom: 10px; }
	@include r(900) {
		@include clr;
		position: relative;
		width: 100%;
		padding: 25px 0 25px 10px; }
	@include r(768) {
		.filters__left {
			width: 120px; } } }
.results {
	background: #fff;
	padding: 30px;
	.results__list {
		counter-reset: list; }
	@include r(768) {
		padding: 20px; }
	@include r(568) {
		padding: 10px; } }
.result {
	position: relative;
	margin-top: 30px;
	@include clr;
	padding-left: 5px;
	list-style-type: none;
	&:before {
		position: absolute;
		top: 25px;
		right: 100%;
		counter-increment: list 1;
		content: counter(list)"."; }
	&:first-child {
		margin-top: 0; }
	a {
		display: block;
		color: $cl; }
	.result__img {
		float: left;
		width: 150px;
		margin-right: 30px;
		img {
			display: block;
			width: 100%; } }
	.result__content {
		overflow: hidden; }
	@include r(768) {
		margin-top: 20px;
		.result__img {
			margin-right: 20px; } }
	@include r(568) {
		.result__img {
			margin-right: 10px;
			width: 120px; } } }

