// rating
.rating {
	margin-bottom: 20px;
	@include clr;
	.rating__title {
		@include r(600) {
			float: left;
			width: 50%; } }
	.rating__item {
		@include clr;
		.jq-ry-container {
			display: inline-block;
			vertical-align: middle; }
		@include r(600) {
			float: right;
			width: 50%;
			text-align: right; } }
	.rating__stars {
		position: relative;
		float: left;
		font-size: 0;
		.star {
			position: relative;
			&:nth-child(8), &:nth-child(9), &:nth-child(10) {
				color: #734fa0;
				&:after {
					content: "";
					position: absolute;
					top: 1px;
					left: 1px;
					color: #734fa0;
					display: none;
					width: 28px;
					height: 28px;
					background: url("../img/star-full.svg");
					@include cover; }
				&.active {
					&:after {
						display: block; } } }
			@include r(1200) {
				&:nth-child(8), &:nth-child(9), &:nth-child(10) {
					&:after {
						top: 0;
						left: 0;
						width: 25px;
						height: 25px; } } }
			@include r(1024) {
				&:nth-child(8), &:nth-child(9), &:nth-child(10) {
					&:after {
						top: 0;
						left: 0;
						width: 17px;
						height: 17px; } } }
			@include r(900) {
				&:nth-child(8), &:nth-child(9), &:nth-child(10) {
					&:after {
						top: 1px;
						left: 1px;
						width: 28px;
						height: 28px; } } }
			@include r(768) {
				&:nth-child(8), &:nth-child(9), &:nth-child(10) {
					&:after {
						top: 1px;
						left: 1px;
						width: 17px;
						height: 17px; } } } } }
	.rating__value {
		float: right;
		font-size: 26px;
		font-weight: 900;
		color: #353535;
		@include r(1200) {
			font-size: 20px; }
		@include r(600) {
			display: inline-block; } }
	@include r(1200) {
		.star {
			font-size: 25px; } }
	@include r(1024) {
		.star {
			font-size: 17px; }
		.rating__value {
			font-size: 16px; } }
	@include r(900) {
		.star {
			font-size: 28px; }
		.rating__value {
			font-size: 20px; } }
	@include r(768) {
		.rating__stars {
			float: none;
			display: inline-block;
			vertical-align: middle; }
		.rating__value {
			float: none;
			display: inline-block;
			vertical-align: middle;
			text-align: center; }
		.star {
			font-size: 20px; } }
	@include r(600) {
		.rating__stars {
			display: inline-block;
			vertical-align: middle;
			width: auto;
			.star {
				display: none;
				font-size: 13px; } }
		.rating__value {
			font-size: 14px; } }
	&.rating_small {
		.star {
			font-size: 17px;
			color: #d9d9d9;
			&.active {
				color: #f8b636; }
			&:nth-child(8), &:nth-child(9), &:nth-child(10) {
				color: #d9d9d9;
				&:after {
					display: none; }
				&.active {
					color: #f8b636; } } }
		.rating__value {
			font-size: 16px; }
		@include r(900) {
			.star {
				font-size: 28px; }
			.rating__value {
				font-size: 20px; } }
		@include r(768) {
			.rating__item {
				float: none;
				width: 100%; }
			.rating__stars {
				display: block;
				text-align: center;
				.star {
					font-size: 20px;
					display: inline-block; } }
			.rating__value {
				font-size: 30px;
				display: block;
				text-align: center; } } }
	&.rating_rotten {
		.rating__stars {
			.star {
				color: red;
				&.active {
					color: green; }
				&:nth-child(8), &:nth-child(9), &:nth-child(10) {
					color: red;
					&:after {
						display: none; }
					&.active {
						color: green; } } } } } }

.star {
