// styles for all inner pages from old design
.old, .overlay {
	position: relative;
	font-family: 'Roboto', sans-serif;
	font-size: 1.4rem;
	line-height: 1.4;
	color: #000;
	background: #e9e9e9;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	z-index: 1;

	// blocks
	@mixin center {
		max-width: 1150px;
		margin: 0 auto;
		padding: 0;
		@include clr;
		@include r(1150) {
			padding: 0 20px; }
		@include r(768) {
			padding: 0 10px; } }
	@mixin justify {
		text-align: justify;
		line-height: 0;
		font-size: 1px;
		text-justify: newspaper;
		zoom: 1;
		text-align-last: justify;
		&:after {
			content: "";
			display: inline-block;
			width: 100%;
			height: 0px;
			visibility: hidden;
			overflow: hidden; } }
	@mixin justify-child {
		display: inline-block;
		line-height: normal;
		vertical-align: top;
		font-size: 1.4rem; }
	.center {
		position: relative;
		@include center; }
	.clr {
		@include clr; }
	.btn {
		display: inline-block;
		max-width: 100%;
		height: 42px;
		padding: 11px 30px;
		border: 2px solid #fff;
		border-radius: 3px;
		text-align: center;
		text-transform: uppercase;
		font-weight: 500;
		text-decoration: none;
		color: #fff;
		font-size: 1.4rem;
		transition: color 300ms, background 300ms;
		span {
			display: inline-block;
			vertical-align: middle; }
		i {
			margin-right: 13px; }
		&:hover {
			color: $dark;
			background: #fff; }
		@include r(568) {
			padding: 11px 20px; }
		&.btn_more {
			border-color: #adafb1;
			color: $dark;
			&:hover {
				background: #adafb1;
				color: #fff; } }
		&.btn_blue {
			border-color: #247bd7;
			color: #247bd7;
			&:hover {
				background: #247bd7;
				color: #fff; } }
		&.btn_purple {
			border-color: #9b1d4f;
			color: #9b1d4f;
			&:hover {
				background: #9b1d4f;
				color: #fff; } }
		&.btn_action {
			border-color: #ab0621;
			color: #ab0621;
			&:hover {
				background: #ab0621;
				color: #fff; } }
		&.btn_comedy {
			border-color: #e66d20;
			color: #e66d20;
			&:hover {
				background: #e66d20;
				color: #fff; } }
		&.btn_megahit {
			border-color: #004681;
			color: #004681;
			&:hover {
				background: #004681;
				color: #fff; } }
		&.btn_premium {
			border-color: #352361;
			color: #352361;
			&:hover {
				background: #352361;
				color: #fff; } }
		&.btn_nature {
			border-color: #618005;
			color: #618005;
			&:hover {
				background: #618005;
				color: #618005; } }
		&.btn_full {
			height: 55px;
			padding: 15px 30px;
			background: #fff;
			color: $dark;
			&:hover {
				background: transparent;
				color: #fff; }
			&.btn_purple {
				background: #9b1d4f;
				color: #fff;
				&:hover {
					background: #cf2669;
					border-color: #cf2669; }
				&.btn_small {
					height: 42px;
					padding: 11px 30px; } }
			&.btn_blue {
				border-color: #247bd7;
				background: #247bd7;
				color: #fff;
				&:hover {
					background: #fff;
					color: #247bd7; } }
			&.btn_black {
				border-color: #000;
				background: #000;
				color: #fff;
				&:hover {
					background: #fff;
					color: #000; } } } }
	.tag {
		display: inline-block;
		vertical-align: middle;
		padding: 3px 10px;
		background: #9b1d4f;
		color: #fff;
		font-size: 1.3rem; }

	// titles
	.h1 {
		margin-bottom: 20px;
		font-size: 3.6rem;
		font-weight: 900;
		color: $dark;
		&.h1_white {
			color: #fff; }
		span {
			font-weight: 300; } }

	.h2 {
		font-size: 3rem;
		font-weight: bold;
		color: $dark;
		&.h2_white {
			color: #fff; } }
	h3, .h3 {
		margin-bottom: 10px;
		font-size: 2.4rem;
		font-weight: bold;
		color: $dark;
		&.h3_white {
			color: #fff; } }
	h4, .h4 {
		margin-bottom: 15px;
		font-size: 2rem;
		font-weight: bold;
		color: $dark; }
	.h6 {
		margin-bottom: 15px;
		font-size: 1.8rem;
		font-weight: bold;
		color: $black;
		text-transform: uppercase; }
	.divide {
		height: 1px;
		background: #d1d1d1;
		margin: 22px 0; }
	.title {
		position: absolute;
		top: 42px;
		left: -32px;
		height: 62px;
		background: #fff;
		z-index: 3;
		&:after {
			content: "";
			position: absolute;
			top: 0;
			left: 100%;
			@include arr(62, 62, #fff, bl); }
		&:before {
			content: "";
			position: absolute;
			top: 100%;
			left: 0;
			@include arr(32, 32, #acb4bf, tr); }
		@include r(480) {
			top: 30px;
			left: -10px;
			height: 40px;
			&:after {
				@include arr(40, 40, #fff, bl); }
			&:before {
				@include arr(10, 10, #acb4bf, tr); } }
		.title__shadow {
			position: absolute;
			top: 55px;
			height: 13px;
			left: 26px;
			right: -50px;
			z-index: -1;
			background: rgba(#000, 0.4);
			transform: rotate(-3deg);
			@include r(480) {
				top: 34px;
				height: 10px;
				right: -28px; } }
		span {
			display: block;
			height: 62px;
			background: #fff;
			padding: 6px 42px 6px 59px;
			font-size: 3.6rem;
			text-transform: uppercase;
			color: $black;
			@include r(1150) {
				font-size: 3rem;
				padding: 10px 42px 10px 59px; }
			@include r(568) {
				font-size: 2rem;
				padding: 16px 42px 16px 59px; }
			@include r(480) {
				height: 40px;
				padding: 7px 42px; } }
		&.title_small {
			height: 32px;
			top: -32px;
			left: -20px;
			&:after {
				content: "";
				position: absolute;
				top: 0;
				left: 100%;
				@include arr(32, 32, #fff, bl); }
			&:before {
				content: "";
				position: absolute;
				top: 100%;
				left: 0;
				@include arr(20, 20, #898e78, tr); }
			span {
				height: 32px;
				font-weight: bold;
				font-size: 1.4rem; }
			@include r(568) {
				span {
					padding: 8px 42px 8px 59px; } } } }
	.topper {
		padding: 0; }
	// breadcrumbs
	.breadcrumbs {
		margin-bottom: 15px;
		text-align: left;
		&.breadcrumbs_top {
			margin-top: 15px;
			margin-bottom: 10px; }
		ul {
			font-size: 0;
			li {
				position: relative;
				display: inline-block;
				vertical-align: middle;
				margin-left: 12px;
				&:before {
					content: "/";
					position: absolute;
					top: 0px;
					left: -11px;
					color: #979797;
					font-size: 1.3rem; }
				&:first-child {
					margin-left: 0;
					&:before {
						display: none; } }
				&:last-child {
					a {
						text-decoration: none; } }
				a {
					color: #979797;
					font-size: 1.3rem;
					text-decoration: underline;
					&:hover {
						text-decoration: none; } } } } }

	// grid
	.row {
		@include clr;
		margin-left: -2%;
		font-size: 0px;
		& > div, & > a {
			margin-left: 2%; }
		@include r(479) {
			margin-left: 0;
			& > div, & > a {
				margin-left: 0; } } }
	.col2 {
		float: left;
		width: 48%;
		@include r(767) {
			margin-bottom: 2%; }
		@include r(479) {
			width: 100%; }
		@include r(360) {
			margin-bottom: 10px; } }

	.col4 {
		// float: left
		display: inline-block;
		width: 23%;
		@include r(767) {
			width: 48%;
			margin-bottom: 2%;
			&.wide {
				width: 98%;
				margin-bottom: 0; } }
		@include r(479) {
			width: 100%;
			&.wide {
				width: 100%; } }
		@include r(360) {
			margin-bottom: 10px; } }
	.main {
		position: relative;
		z-index: 2;
		float: left;
		width: 65.7%;
		padding-top: 20px;
		padding-bottom: 70px;
		@include r(960) {
			width: 100%;
			padding-bottom: 20px; } }
	.sidebar {
		position: relative;
		z-index: 1;
		float: right;
		width: 31.7%;
		padding-top: 40px;
		&.sidebar_low {
			padding-top: 120px; }
		.info {
			text-align: center; }
		@include r(960) {
			width: 100%;
			padding-top: 0;
			margin-bottom: 20px;
			&.sidebar_low {
				padding-top: 0; } } }
	.banner {
		img {
			width: 100%; }
		@include r(960) {
			float: right;
			width: 45%; }
		@include r(480) {
			width: 100%; } }
	.fb-widget {
		margin-bottom: 20px;
		& > img {
			display: block;
			width: 100%; }
		@include r(960) {
			width: 45%;
			float: left; }
		@include r(480) {
			width: 100%; } }

	.soc-widget {
		@include clr; }
	.soc-widget {
		display: block;
		width: 100%;
		margin-bottom: 20px;
		iframe {
			max-width: 100%;
			width: 100%; }
		@include r(960) {
			width: 45%; }
		@include r(767) {
			width: 100%; } }


	.channels {
		.channels__item {
			position: relative;
			margin-top: 30px;
			background: #FFF;
			padding: 340px 30px 50px;
			&:first-child {
				margin-top: 0; }
			@include r(1150) {
				padding: 340px 15px 50px; }
			@include r(768) {
				padding: 340px 15px 15px; }
			@include r(568) {
				padding: 300px 15px 15px; }
			@include r(479) {
				padding: 300px 15px 0; }
			@include r(414) {
				padding: 215px 15px 0; }
			@include r(320) {
				padding: 180px 15px 0; }
			.channels__films, .channels__discovery, .channels__sport {
				position: absolute;
				top: 0;
				bottom: 0;
				right: 0;
				left: 0;
				font-size: 0;
				background: #605f6c;
				img {
					position: relative;
					width: 100%;
					z-index: 1; }
				.channels__fon {
					position: relative; }
				.channels__bg {
					position: absolute;
					bottom: 0;
					height: 100px;
					right: 0;
					left: 0;
					@include g(rgba(#605f6c, 0), rgba(#605f6c, 1));
					z-index: 2; } }
			.channels__discovery {
				background: #a19d3f;
				.channels__bg {
					@include g(rgba(#a19d3f, 0), rgba(#a19d3f, 1)); } }
			.channels__sport {
				background: #000;
				.channels__bg {
					@include g(rgba(#000, 0), rgba(#000, 1)); } } }
		.channels__list {
			position: relative;
			margin-left: -4%;
			margin-bottom: -4%;
			@include clr;
			z-index: 2;
			@include r(1150) {
				margin-left: -2%;
				margin-bottom: -2%; }
			@include r(960) {
				font-size: 0; }
			@include r(479) {
				margin-left: 0;
				margin-bottom: 0; } }
		.channels__block {
			float: left;
			width: 46%;
			height: 0;
			margin-left: 4%;
			margin-bottom: 4%;
			padding-bottom: 39%;
			background: #fff;
			@include r(1150) {
				width: 48%;
				margin-left: 2%;
				margin-bottom: 2%; }
			@include r(960) {
				float: none;
				display: inline-block;
				vertical-align: top;
				height: auto;
				padding-bottom: 0; }
			@include r(479) {
				width: 100%;
				margin-left: 0;
				margin-bottom: 15px; } }
		.channels__in {
			padding: 30px;
			& > img {
				display: inline-block;
				max-width: 100%;
				margin-bottom: 15px; }
			p {
				margin-bottom: 15px;
				font-size: 1.4rem;
				color: $dark; }
			@include r(1150) {
				padding: 15px;
				& > img {
					margin-bottom: 10px; }
				p {
					margin-bottom: 10px; } } }
		.channels__more {
			text-align: right;
			a {
				color: $black;
				font-size: 1.4rem;
				font-weight: 500;
				text-decoration: none;
				&:hover {
					text-decoration: underline; } } } }

	// widget
	.widget {
		padding: 24px 33px 37px;
		background: #efefef;
		.slick-dots {
			text-align: center;
			position: absolute;
			margin: 0;
			bottom: -30px;
			left: 0;
			right: 0; }
		.slick-prev,
		.slick-next {
			width: 30px;
			height: 30px;
			border-radius: 50%;
			color: $cl; }
		.slick-prev {
			left: -32px;
			@include r(1160) {
				left: -30px; }
			@include r(768) {
				left: -15px; }
			&:before {
				position: absolute;
				top: 0;
				left: 0;
				width: 30px;
				height: 30px;
				border-radius: 50%;
				border: 2px solid #dadada;
				background: url("../img/arr-left-gray.svg") center no-repeat; }
			&:after {
				content: "";
				opacity: 0;
				position: absolute;
				top: 0;
				left: 0;
				width: 30px;
				height: 30px;
				border-radius: 50%;
				border: 2px solid #dadada;
				background: url("../img/arr-left-white.svg") center no-repeat; }
			&:hover {
				background: #dadada;
				&:before {
					opacity: 0; }
				&:after {
					opacity: 1; } } }
		.slick-next {
			right: -32px;
			@include r(1160) {
				right: -30px; }
			@include r(768) {
				right: -15px; }
			&:before {
				position: absolute;
				top: 0;
				left: 0;
				width: 30px;
				height: 30px;
				border-radius: 50%;
				border: 2px solid #dadada;
				background: url("../img/arr-right-gray.svg") center no-repeat; }
			&:after {
				opacity: 0;
				position: absolute;
				top: 0;
				left: 0;
				width: 30px;
				height: 30px;
				border-radius: 50%;
				border: 2px solid #dadada;
				background: url("../img/arr-right-white.svg") center no-repeat;
				z-index: 3; }
			&:hover {
				background: #dadada;
				&:before {
					opacity: 0; }
				&:after {
					opacity: 1; } } }
		@include r(960) {
			padding: 24px 20px 37px;
			.slick-slide {
				padding: 0 16px; }
			.poster__content {
				padding: 10px; } } }
	.subscr, .feedback {
		display: block;
		height: 55px;
		margin-bottom: 20px;
		padding: 13px 33px;
		border-radius: 3px;
		background: #fff url(../img/subscr.png) 90% 100% no-repeat;
		font-size: 1.8rem;
		color: $dark;
		text-decoration: none;
		font-weight: bold; }
	.feedback {
		background: #fff url(../img/feedback.png) 90% 100% no-repeat; }

	// 404 error
	.error {
		padding: 125px 10px 185px;
		text-align: center;
		@include r(768) {
			padding: 100px 10px 150px; }
		@include r(600) {
			padding: 80px 10px 120px; }
		@include r(480) {
			padding: 50px 10px; }
		&__img {
			margin-bottom: 60px;
			font-size: 0;
			img {
				max-width: 100%; } }
		&__content {
			max-width: 520px;
			margin: 0 auto;
			p {
				margin-bottom: 25px;
				font: 1.6rem Arial, sans-serif; } }
		&__title {
			margin-bottom: 10px;
			font: bold 2.4rem Arial, sans-serif; }
		.error__links {
			a, span {
				display: inline-block;
				vertical-align: middle;
				@include r(480) {
					display: block;
					margin: 0 auto 20px; } }
			span {
				margin: 0 20px;
				font-size: 1.4rem;
				font-weight: 500; } } }
	.recommends {
		background: #504f58;
		padding: 28px 0 48px;
		overflow: hidden;
		& > .center {
			padding: 0 28px;
			@include r(768) {
				padding: 0 15px; }
			@include r(640) {
				padding: 0 10px; } }
		&.recommends_action {
			background: #ab0621; }
		&.recommends_comedy {
			background: #E66D20; }
		&.recommends_megahit {
			background: #004681; }
		&.recommends_premium {
			background: #352361; }
		&.recommends_nature {
			background: #618005; }
		.recommends__title {
			margin-bottom: 20px;
			padding-left: 10px;
			font-size: 2.4rem;
			color: #fff;
			text-transform: uppercase;
			font-weight: 300;
			span {
				font-weight: 500; }
			@include r(768) {
				padding-left: 0; }
			@include r(480) {
				text-align: center; } }
		.slick-list {
			margin: 0 -11px; }
		.poster {
			width: 266px;
			margin: 0 auto;
			& > img {
				min-height: 145px; } } }

	// blog
	.blog {
		padding-top: 20px;
		.blog__more {
			text-align: center; } }
	.blog-category {
		margin-top: 30px;
		padding: 9px 0;
		border: 1px solid #9e9e9e;
		text-align: center;
		font-size: 3.6rem;
		text-transform: uppercase;
		@include r(768) {
			font-size: 3rem; }
		@include r(480) {
			font-size: 2.4rem; } }
	.post {
		@include clr;
		margin-bottom: 30px;
		.btn.btn_full.btn_purple {
			&:hover {
				background: $dark;
				border-color: $dark; } }
		.post__info {
			float: left;
			width: 90px;
			text-align: center;
			padding-right: 12px;
			.socials {
				&.fixed {
					position: fixed;
					top: 20px;
					width: 92px; }
				&.is-bottom {
					position: absolute;
					top: auto;
					bottom: 42px;
					width: 78px; } }
			@include r(480) {
				float: none;
				width: 100%;
				@include clr;
				padding-right: 0;
				margin-bottom: 8px;
				.socials {
					font-size: 0;
					margin-left: -3%;
					text-align: left;
					& > div {
						font-size: 1.1rem; }
					.fb-counter, .gp-counter, .tw-counter, .od-counter, .vk-counter {
						display: inline-block;
						vertical-align: middle;
						width: 30%;
						margin-left: 3%;
						font-size: 1.1rem; } } } }
		.post__date {
			font-size: 1.4rem;
			padding-bottom: 5px;
			color: $dark;
			border-bottom: 1px solid #d7d7d7;
			margin-bottom: 12px;
			p {
				font-size: 4.8rem;
				font-weight: 900;
				line-height: 1; }
			@include r(480) {
				margin: 0 auto 12px;
				p {
					font-size: 4rem; } } }
		.post__in {
			padding: 20px 0 30px;
			& > p {
				margin-bottom: 25px;
				padding: 0 5%; }
			& > h3 {
				padding: 0 5%; }
			@include r(480) {
				padding: 15px;
				& > .h3 {
					font-size: 2rem; } }
			.article a {
				color: $violet; } }
		.post__content {
			overflow: hidden;
			background: #FFF;
			& > img {
				width: 100%;
				display: block; }
			@include r(480) {
				overflow: visible; } }
		.post__details {
			@include clr;
			padding: 0 30px;
			margin-bottom: 18px; }
		.post__left {
			float: left; }
		.post__right {
			float: right;
			text-align: right;
			height: 25px;
			line-height: 25px; }
		.post__views, .post__comments {
			display: inline-block;
			vertical-align: middle;
			font-size: 1.2rem;
			color: #9e9e9e;
			margin-left: 12px; }
