// popups
.overlay {
	position: fixed;
	top: 0;
	bottom: 0;
	right: 0;
	left: 0;
	padding: 80px 0 20px;
	background: rgba(#000, 0.7);
	z-index: 999;
	overflow-y: auto;
	@include vertical;
	& > span {
