		top: 0;
		bottom: 0;
		right: 0;
		left: 0;
		z-index: 1; } }
.popup {
	position: relative;
	display: inline-block;
	vertical-align: middle;
	width: 700px;
	max-width: 100%;
	z-index: 2;
