// gallery
.gallery-overlay {
	position: fixed;
	top: 0;
	bottom: 0;
	right: 0;
	left: 0;
	background: rgba(#000, 0.9);
	z-index: 999;
	@include r(800) {
		@include vertical; } }
.close {
	position: absolute;
	top: 20px;
	right: 20px;
	background: transparent;
	z-index: 2;
	i, svg {
		color: #fff;
		font-size: 24px; } }
.gallery {
	position: absolute;
	top: 0;
	bottom: 0;
	right: 0;
	left: 0;
	padding: 55px 0;
	overflow-y: auto;
	text-align: center;
	z-index: 1;
	.slick-prev,
	.slick-next {
		position: absolute;
		top: 50%;
		margin-top: -7px;
		@include hide-text;
		height: 14px;
		background: none;
		z-index: 2;
		&:focus {
			outline: none; }
		&.slick-disabled {
			opacity: 0.2; }
		&:before {
			height: 14px;
			line-height: 14px; }
		&:hover {
			&:before {
				opacity: 1; } } }
	.slick-prev {
		left: 14px;
		&:before {
			content: "\e60e";
			display: inline-block;
			vertical-align: middle;
			font-size: 30px;
			font-family: 'icomoon';
			color: #fff; }
		@include r(1024) {
			left: 5px; } }
	.slick-next {
		right: 14px;
		&:before {
			content: "\e60b";
			display: inline-block;
			vertical-align: middle;
			font-family: 'icomoon';
			font-size: 30px;
			color: #fff;
			@include r(1024) {
				right: 5px; } } }
	@include r(1024) {
		padding: 55px 0; }
	@include r(800) {
		position: relative;
		top: auto;
		bottom: auto;
		right: auto;
		left: auto;
		padding-top: 0;
		width: 100%;
		display: inline-block;
		vertical-align: middle;
		.slick-slide {
			height: auto;
			img {
				width: 100%;
				height: auto; } } } }
.gallery__view {
	margin-bottom: 20px;
	.slick-slide {
		text-align: center;
		img {
			display: inline-block;
			max-width: 100%;
			width: auto;
			height: 400px; }
		@include r(768) {
			img {
				height: auto; } } } }
.gallery__thumbnail {
	display: inline-block;
	width: 800px;
	text-align: center;
	@include r(800) {
		display: none; }
	.slick-slide {
		position: relative;
		padding: 0 5px;
		height: 40px;
		width: 75px;
		cursor: pointer;
		&.slick-current {
			&:before {
				content: "";
				position: absolute;
				top: 0;
				left: 12px;
				right: 12px;
				bottom: 0;
				border: 2px solid #fff;
				z-index: 2; } } } }
